@font-face {
  font-family: "archia";
  src: local("AssistantRegular"),
    url("./assets/Archia_Regular/Archia\ Regular\ Webfont/archia-regular-webfont.ttf") format("truetype");
  font-weight: normal;
}



*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: archia;
}

.maincontainer{
  padding-top: 3.5rem;
}

.date-time{
    color: #6c757d;
    font-weight: 600;
    margin: 0;
    font-size: 12px;
    text-align: center;
    padding: 1rem 0;
}

.counter-container{
  margin-top: 3rem;
}

.counter-wrapper h5{
  font-weight: 600;
  overflow-wrap: break-word;
  text-align: center;
  width: 100%;
  font-size: 12px;
}

.counter-wrapper h4{
  margin-top: 1rem;
  text-align: center;
  font-size: 12.5px;
}

.counter-wrapper h1{
  font-size: min(3.75vw,22px)!important;
  font-weight: 600;
  text-align: center;
  margin-top: 0.5rem;
}

.text-danger{
  color: #ff073a;
}

.text-primary{
  color: #007bff;
}

.text-success{
  color: #28a745;
}

.text-dark{
  color: #6c757d;
}

.table-container{
  margin-top: 3rem;
}

th{
    font-size: 13px;
    font-weight: 900;
    padding: 1.25rem .5rem .75rem;
    color: #6c757d;
    font-weight: 900;
}

@media screen and (max-width: 600px) {
  .counter-wrapper{
    margin-bottom: 2rem!important;
  }

  .table-container {
    margin-top: 1rem;
  }
}